<template>
  <div>
    <div class="header">
      <div>
        <svg class="waves" xmlns="http://www.w3.org/2000/svg" xmlns:xlink="http://www.w3.org/1999/xlink" viewBox="0 24 150 28" preserveAspectRatio="none" shape-rendering="auto">
          <defs>
            <path id="gentle-wave" d="M-160 44c30 0 58-18 88-18s 58 18 88 18 58-18 88-18 58 18 88 18 v44h-352z" />
          </defs>
          <g class="parallax">
            <use xlink:href="#gentle-wave" x="48" y="0" fill="rgba(255,255,255,0.7" />
            <use xlink:href="#gentle-wave" x="48" y="3" fill="rgba(255,255,255,0.5)" />
            <use xlink:href="#gentle-wave" x="48" y="5" fill="rgba(255,255,255,0.3)" />
            <use xlink:href="#gentle-wave" x="48" y="7" fill="#fff" />
          </g>
        </svg>
      </div>
    </div>
    <div class="container pt-3">
      <h2>{{container.title}}</h2>
      <p class="date">{{container.date}}</p>
      <p v-html="container.content"></p>
    </div>
  </div>
</template>

<script>
export default {
  data () {
    return {
      container: [],
    }
  },
  mounted () {
    this.getContainerData();
  },
  methods: {
    getContainerData: function () {
      var data = location.hash.split('container?')[1].split('&')[0];
      var type = data ;
      if ( data === 'highlight' || data === 'course' || data === 'speech') {
        type = 'news'
      }
      /**https://cors-anywhere.herokuapp.com/ */
      var url = 'api/' + type + '/' + data + '.json';
      this.$axios.get(url).then(res=>{
        var data = res.data;
        var title = location.hash.split('container?')[1].split('&')[1];
        for (var i = 0; i < data.length; i++) {
          if ( title === encodeURIComponent(data[i].title)){
            this.container = data[i];
          }
        }
      })
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
@import url(//fonts.googleapis.com/css?family=Lato:300:400);

h2 {
  text-align: left;
  font-weight: 600;
}

.date {
  color:#376c8a
}

p {
  text-align: left;
}

.header {
  text-align:center;
  background: linear-gradient(90deg,#fec832 50%, #ff5657 50%);
}

.flex { /*Flexbox for containers*/
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  text-align: center;
}

.waves {
  position:relative;
  width: 100%;
  height:15vh;
  margin-bottom:-7px; /*Fix for safari gap*/
  min-height:100px;
  max-height:150px;
}

/* Animation */

.parallax > use {
  animation: move-forever 25s cubic-bezier(.55,.5,.45,.5)     infinite;
}
.parallax > use:nth-child(1) {
  animation-delay: -2s;
  animation-duration: 7s;
}
.parallax > use:nth-child(2) {
  animation-delay: -3s;
  animation-duration: 10s;
}
.parallax > use:nth-child(3) {
  animation-delay: -4s;
  animation-duration: 13s;
}
.parallax > use:nth-child(4) {
  animation-delay: -5s;
  animation-duration: 20s;
}
@keyframes move-forever {
  0% {
   transform: translate3d(-90px,0,0);
  }
  100% { 
    transform: translate3d(85px,0,0);
  }
}
/*Shrinking for mobile*/
@media (max-width: 768px) {
  .waves {
    height:40px;
    min-height:40px;
  }
  .content {
    height:30vh;
  }
  h2 {
    font-size: 24px;
  }
  p {
    width:100%;
  }
}
</style>
