<template>
  <div>
    <Title title='recruit' />
    <div class="container">
    <Carousel
      :img1="'http://www.math.fcu.edu.tw/ds/api/recruit/recruit_carousel_img/img1.png?' + this.nowTime"
      :img2="'http://www.math.fcu.edu.tw/ds/api/recruit/recruit_carousel_img/img2.png?' + this.nowTime"
      :img3="'http://www.math.fcu.edu.tw/ds/api/recruit/recruit_carousel_img/img3.png?' + this.nowTime"
      :img4="'http://www.math.fcu.edu.tw/ds/api/recruit/recruit_carousel_img/img4.png?' + this.nowTime"
      :img5="'http://www.math.fcu.edu.tw/ds/api/recruit/recruit_carousel_img/img5.png?' + this.nowTime"
      />
      <ul class="nav nav-tabs pt-4" id="myTab" role="tablist">
        <li class="nav-item">
          <a class="nav-link active" id="recruit-tab" data-toggle="tab" href="#recruit" role="tab" aria-controls="recruit" aria-selected="true">招生資訊</a>
        </li>
        <li class="nav-item">
          <a class="nav-link" id="highlight-tab" data-toggle="tab" href="#highlight" role="tab" aria-controls="highlight" aria-selected="false">學程亮點</a>
        </li>
      </ul>
      <div class="tab-content" id="myTabContent">
        <div class="tab-pane fade show active" id="recruit" role="tabpanel" aria-labelledby="recruit-tab"><TablePage :datas=recruit label='recruit'/></div>
        <div class="tab-pane fade" id="highlight" role="tabpanel" aria-labelledby="highlight-tab"><TablePage :datas=highlight label='highlight'/></div>
      </div>
    </div>
  </div>
</template>

<script>
import Title from '../components/Title'
import TablePage from '../components/Table'
import Carousel from '../components/Carousel'
export default {
  components: {
    Title,
    TablePage,
    Carousel
  },
  data() {
    return {
      recruit: [],
      highlight: [],
      nowTime: String,
    }
  },
  created: function() {
    this.nowTime = Date.now();
  },
  mounted() {
    this.getRecruittData();
    this.getHighlightData();
  },
  methods: {
    /**https://cors-anywhere.herokuapp.com/http://140.134.140.35/ds/ */
    getRecruittData: function () {
      this.$axios.get('api/recruit/recruit.json').then(res=>{       
        this.recruit = res.data
      })
    },
    getHighlightData: function () {
      this.$axios.get('api/news/highlight.json').then(res=>{       
        this.highlight = res.data
      })
    },
  }
}
</script>

