import Vue from 'vue'
import Router from 'vue-router'
import Home from '../pages/Home'
import Administrative from '../pages/Administrative'
import Course from '../pages/Course'
import Faculty from '../pages/Faculty'
import Highlight from '../pages/Highlight'
import News from '../pages/News'
import Recruit from '../pages/Recruit'
import Teacher from '../pages/Teacher'
import TeacherContainer from '../pages/TeacherContainer'
import About from '../pages/About'
import Container from '../components/Container'

Vue.use(Router)

export default new Router({
    routes: [
      { path: '/', name: 'Home', component: Home},
      { path: '/teacher/administrative', name: 'Administrative', component: Administrative},
      { path: '/course', name: 'Course', component: Course},
      { path: '/teacher/faculty', name: 'Faculty', component: Faculty},
      { path: '/highlight', name: 'Highlight', component: Highlight},
      { path: '/news', name: 'News', component: News},
      { path: '/recruit', name: 'Recruit', component: Recruit},
      { path: '/teacher', name: 'Teacher', component: Teacher},
      { path: '/teacher/container', name: 'TeacherContainer', component: TeacherContainer},
      { path: '/about', name: 'About', component: About},
      { path: '/container', name: 'Container', component: Container}
    ],
    scrollBehavior () {
      // return 期望滚动到哪个的位置
      return { x: 0, y: 0 }
    }
  })