<template>
  <div>
    <div class="header">
      <div class="inner-header flex"></div>
      <div>
        <svg
          class="waves"
          xmlns="http://www.w3.org/2000/svg"
          xmlns:xlink="http://www.w3.org/1999/xlink"
          viewBox="0 24 150 28"
          preserveAspectRatio="none"
          shape-rendering="auto"
        >
          <defs>
            <path
              id="gentle-wave"
              d="M-160 44c30 0 58-18 88-18s 58 18 88 18 58-18 88-18 58 18 88 18 v44h-352z"
            />
          </defs>
          <g class="parallax">
            <use
              xlink:href="#gentle-wave"
              x="48"
              y="0"
              fill="rgba(255,255,255,0.7"
            />
            <use
              xlink:href="#gentle-wave"
              x="48"
              y="3"
              fill="rgba(255,255,255,0.5)"
            />
            <use
              xlink:href="#gentle-wave"
              x="48"
              y="5"
              fill="rgba(255,255,255,0.3)"
            />
            <use xlink:href="#gentle-wave" x="48" y="7" fill="#fff" />
          </g>
        </svg>
      </div>
    </div>
    <div class="container">
      <img
        :src="
          'http://140.134.140.35/ds/api/teacher/get_ds_data/img/' +
          teacher.emp_id +
          '.png?202010152007'
        "
      />
      <div class="card-title mt-3">
        <h3>{{ teacher.name }}</h3>
        <h4>| {{ teacher.tit_name }}</h4>
      </div>
      <div class="accordion" id="accordionExample">
        <div class="card">
          <div class="card-header" id="headingOne">
            <h2 class="mb-0">
              <button
                class="btn btn-link btn-block text-left"
                type="button"
                data-toggle="collapse"
                data-target="#collapseOne"
                aria-expanded="true"
                aria-controls="collapseOne"
              >
                專長/學歷
              </button>
            </h2>
          </div>

          <div
            id="collapseOne"
            class="collapse show"
            aria-labelledby="headingOne"
            data-parent="#accordionExample"
          >
            <div class="card-body">
              <h4 v-if="teacher.skill.length !== 0">專長</h4>
              <li v-for="(skill, i) in teacher.skill" :key="i">
                {{ skill.ski_desc }}
              </li>
              <h4 class="mt-4" v-if="teacher.edu.length !== 0">學歷</h4>
              <li v-for="(edu, i) in teacher.edu" :key="i">
                {{ edu.edu_xmcname }} | {{ edu.edu_xddname }}
              </li>
            </div>
          </div>
        </div>
        <div
          class="card"
          v-if="teacher.Expfcu.length || teacher.Expother.length"
        >
          <div class="card-header" id="headingTwo">
            <h2 class="mb-0">
              <button
                class="btn btn-link btn-block text-left collapsed"
                type="button"
                data-toggle="collapse"
                data-target="#collapseTwo"
                aria-expanded="false"
                aria-controls="collapseTwo"
              >
                校內/外經驗
              </button>
            </h2>
          </div>
          <div
            id="collapseTwo"
            class="collapse"
            aria-labelledby="headingTwo"
            data-parent="#accordionExample"
          >
            <div class="card-body">
              <h4 v-if="teacher.Expfcu.length">校內經驗</h4>
              <li v-for="(Expfcu, i) in teacher.Expfcu" :key="i">
                {{ Expfcu.unt_lname }} | {{ Expfcu.tit_name }}
              </li>
              <h4 class="mt-4" v-if="teacher.Expother.length">校外經驗</h4>
              <li v-for="(Expother, i) in teacher.Expother" :key="i">
                {{ Expother.unt_lname || Expother.exp_unitname }} |
                {{ Expother.tit_name || Expother.exp_titname }}
              </li>
            </div>
          </div>
        </div>
        <div class="card" v-if="teacher.Journal.length">
          <div class="card-header" id="headingThree">
            <h2 class="mb-0">
              <button
                class="btn btn-link btn-block text-left collapsed"
                type="button"
                data-toggle="collapse"
                data-target="#collapseThree"
                aria-expanded="false"
                aria-controls="collapseThree"
              >
                發表期刊論文（{{ teacher.Journal.length }}）
              </button>
            </h2>
          </div>
          <div
            id="collapseThree"
            class="collapse"
            aria-labelledby="headingThree"
            data-parent="#accordionExample"
          >
            <div class="card-body" style="text-align: left">
              <li v-for="(Journal, i) in teacher.Journal" :key="i" class="mt-1">
                {{ Journal.jou_autgroup }},{{ Journal.jou_paper }},{{
                  Journal.jou_name
                }},{{ Journal.jou_vol }},{{ Journal.jou_ym }} ({{
                  Journal.v_index
                }})
              </li>
            </div>
          </div>
        </div>
        <div class="card" v-if="teacher.Conference.length">
          <div class="card-header" id="headingFour">
            <h2 class="mb-0">
              <button
                class="btn btn-link btn-block text-left collapsed"
                type="button"
                data-toggle="collapse"
                data-target="#collapseFour"
                aria-expanded="false"
                aria-controls="collapseFour"
              >
                會議論文（{{ teacher.Conference.length }}）
              </button>
            </h2>
          </div>
          <div
            id="collapseFour"
            class="collapse"
            aria-labelledby="headingFour"
            data-parent="#accordionExample"
          >
            <div class="card-body" style="text-align: left">
              <li
                v-for="(Conference, i) in teacher.Conference"
                :key="i"
                class="mt-1"
              >
                {{ Conference.con_autgroup }},{{ Conference.con_paper }},{{
                  Conference.con_name
                }},{{ Conference.con_vol }},{{ Conference.con_stdate }},{{
                  Conference.con_place
                }}
              </li>
            </div>
          </div>
        </div>
        <div class="card" v-if="teacher.Book.length">
          <div class="card-header" id="headingFive">
            <h2 class="mb-0">
              <button
                class="btn btn-link btn-block text-left collapsed"
                type="button"
                data-toggle="collapse"
                data-target="#collapseFive"
                aria-expanded="false"
                aria-controls="collapseFive"
              >
                專書及技術報告（{{ teacher.Book.length }}）
              </button>
            </h2>
          </div>
          <div
            id="collapseFive"
            class="collapse"
            aria-labelledby="headingFive"
            data-parent="#accordionExample"
          >
            <div class="card-body" style="text-align: left">
              <li v-for="(Book, i) in teacher.Book" :key="i" class="mt-1">
                <span style="font-weight: 800">{{ Book.bok_name }}</span> |
                作者：{{ Book.bok_autgroup }} | 出版日：{{ time(Book.bok_ym) }}
              </li>
            </div>
          </div>
        </div>
        <div class="card" v-if="teacher.Tech.length">
          <div class="card-header" id="headingSix">
            <h2 class="mb-0">
              <button
                class="btn btn-link btn-block text-left collapsed"
                type="button"
                data-toggle="collapse"
                data-target="#collapseSix"
                aria-expanded="false"
                aria-controls="collapseSix"
              >
                專書論文（{{ teacher.Tech.length }}）
              </button>
            </h2>
          </div>
          <div
            id="collapseSix"
            class="collapse"
            aria-labelledby="headingSix"
            data-parent="#accordionExample"
          >
            <div class="card-body" style="text-align: left">
              <li v-for="(Tech, i) in teacher.Tech" :key="i" class="mt-1">
                <span style="font-weight: 800">{{ Tech.tec_report }}</span> |
                作者：{{ Tech.tec_autgroup }} | 出版日：{{ time(Tech.tec_ym) }}
              </li>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  components: {},
  data() {
    return {
      teacher: [],
      nowTime: 1,
    };
  },
  mounted() {
    this.getTeacherData();
  },
  methods: {
    /**https://cors-anywhere.herokuapp.com/http://www.math.fcu.edu.tw/ds/ */
    getTeacherData: async function () {
      await this.$axios
        .get(
          "http://www.math.fcu.edu.tw/ds/api/teacher/get_ds_data/Teacher.json"
        )
        .then((res) => {
          var data = res.data;
          for (var i = 0; i < data.length; i++) {
            if (location.hash.indexOf(data[i].emp_id) !== -1) {
              this.teacher = data[i];
              console.log(this.teacher);
            }
          }
        });
    },
    time(date) {
      if (date) {
        return date.slice(0, 10);
      }
    },
  },
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
h4 {
  font-weight: 800;
}
h3,
.detail {
  font-weight: 800;
  margin-right: 5px;
}
h4 {
  color: #495057;
}
img {
  padding-top: 20px;
  width: 250px;
}
.teacher-img {
  overflow: hidden;
}

.card-text {
  text-align: left;
}
p {
  margin: 0px;
}

.card-title {
  display: flex;
  justify-content: center;
  align-items: flex-end;
}
@import url(//fonts.googleapis.com/css?family=Lato:300:400);

.header {
  text-align: center;
  background: linear-gradient(90deg, #fec832 50%, #002060 50%);
  color: white;
}

.inner-header {
  width: 100%;
}

.flex {
  /*Flexbox for containers*/
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  text-align: center;
}

.waves {
  position: relative;
  width: 100%;
  height: 15vh;
  margin-bottom: -7px; /*Fix for safari gap*/
  min-height: 100px;
  max-height: 150px;
}

.content {
  position: relative;
  height: 20vh;
  text-align: center;
  background-color: white;
}

/* Animation */

.parallax > use {
  animation: move-forever 25s cubic-bezier(0.55, 0.5, 0.45, 0.5) infinite;
}
.parallax > use:nth-child(1) {
  animation-delay: -2s;
  animation-duration: 7s;
}
.parallax > use:nth-child(2) {
  animation-delay: -3s;
  animation-duration: 10s;
}
.parallax > use:nth-child(3) {
  animation-delay: -4s;
  animation-duration: 13s;
}
.parallax > use:nth-child(4) {
  animation-delay: -5s;
  animation-duration: 20s;
}
@keyframes move-forever {
  0% {
    transform: translate3d(-90px, 0, 0);
  }
  100% {
    transform: translate3d(85px, 0, 0);
  }
}
/*Shrinking for mobile*/
@media (max-width: 768px) {
  .waves {
    height: 40px;
    min-height: 40px;
  }
  .content {
    height: 30vh;
  }
  h1 {
    font-size: 24px;
  }
}
.mobile {
  display: none;
}
/*Shrinking for mobile*/
@media (max-width: 990px) {
  .inner-header img {
    width: 60%;
  }
  .pc {
    display: none;
  }
  .mobile {
    display: inline-block;
  }
}
</style>
