<template>
  <div>
    <Header/>
    <div class="container pt-3">
      <!-- 輪播牆 -->
      <Carousel
        :img1="'http://www.math.fcu.edu.tw/ds/api/home/home_carousel_img/img1.png?' + this.nowTime"
        :img2="'http://www.math.fcu.edu.tw/ds/api/home/home_carousel_img/img2.png?' + this.nowTime"
        :img3="'http://www.math.fcu.edu.tw/ds/api/home/home_carousel_img/img3.png?' + this.nowTime"
        :img4="'http://www.math.fcu.edu.tw/ds/api/home/home_carousel_img/img4.png?' + this.nowTime"
        :img5="'http://www.math.fcu.edu.tw/ds/api/home/home_carousel_img/img5.png?' + this.nowTime"
        />
      <!-- 訊息快報 -->
      <div class="card mt-3">
        <div class="card-header">訊息快報</div>
        <ul class="nav nav-tabs" id="myTab" role="tablist">
          <li class="nav-item">
            <a class="nav-link active" id="news-tab" data-toggle="tab" href="#news" role="tab" aria-controls="news" aria-selected="true">最新消息</a>
          </li>
          <li class="nav-item">
            <a class="nav-link" id="highlight-tab" data-toggle="tab" href="#highlight" role="tab" aria-controls="highlight" aria-selected="false">學程亮點</a>
          </li>
          <li class="nav-item">
            <a class="nav-link" id="course-tab" data-toggle="tab" href="#course" role="tab" aria-controls="course" aria-selected="false">選課通知</a>
          </li>
          <li class="nav-item">
            <a class="nav-link" id="speech-tab" data-toggle="tab" href="#speech" role="tab" aria-controls="speech" aria-selected="false">學術演講</a>
          </li>
        </ul>
        <div class="tab-content" id="myTabContent">
          <div class="tab-pane fade show active" id="news" role="tabpanel" aria-labelledby="news-tab"><TablePage :datas=news label='news'/></div>
          <div class="tab-pane fade" id="highlight" role="tabpanel" aria-labelledby="highlight-tab"><TablePage :datas=highlight label='highlight'/></div>
          <div class="tab-pane fade" id="course" role="tabpanel" aria-labelledby="course-tab"><TablePage :datas=course label='course'/></div>
          <div class="tab-pane fade" id="speech" role="tabpanel" aria-labelledby="speech-tab"><TablePage :datas=speech label='speech'/></div>
        </div>
        <div class="card-footer p-1">
          <a href="./#/news">more</a>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import Header from '../components/Header'
import TablePage from '../components/Table'
import Carousel from '../components/Carousel'
export default {
  components:{
    Header,
    TablePage,
    Carousel
  },
  data() {
    return {
      news: [],
      highlight: [],
      course: [],
      speech: [],
      nowTime: String,
    }
  },
  created: function() {
    this.nowTime = Date.now();
  },
  mounted() {
    this.getHighlightData();
    this.getNewsData();
    this.getCourseData();
    this.getSpeechData();
  },
  methods: {
    /**https://cors-anywhere.herokuapp.com/http://140.134.140.35/ds/ */
    getHighlightData: function () {
      this.$axios.get('api/news/highlight.json').then(res=>{       
        this.highlight = res.data
      })
    },
    getNewsData: function () {
      this.$axios.get('api/news/news.json').then(res=>{       
        this.news = res.data
      })
    },
    getCourseData: function () {
      this.$axios.get('api/news/course.json').then(res=>{       
        this.course = res.data
      })
    },
    getSpeechData: function () {
      this.$axios.get('api/news/speech.json').then(res=>{       
        this.speech = res.data
      })
    }
  }
}
</script>

<style scoped>
.card {
  border: none;
  border-radius: 10px;
  padding: 10px;
}
.card-header {
  font-weight: 600;
  font-size: 20px;
}
.card-footer a{
  color: #002060;
}
.nav-link {
  color: #002060;
}
.nav-tabs .nav-link.active {
  color:#858d94;
}

.blue, .blue a{
  background-color: #002060;
  color:white;
}
.yellow {
  background-color: #fec832;
  color:#000000;
}

.yellow_light {
  background-color: #fff4d6;
  color:#000000;
}
.blue_light {
  background-color: #dde8ff;
  color: #000000;
}
@media (max-width: 768px) {
  .nav-link {
    padding: 6px 8px;
  }
  .card {
  border-radius: 0px;
}
}
</style>
