<template>
  <div>
    <Title title="teacher" />
    <div class="container">
      <div class="row row-cols-1 row-cols-md-3">
        <div
          class="col p-3 d-inline-flex"
          v-for="term in teacher"
          :key="term.emp_id"
        >
          <div class="card flex-fill">
            <div class="teacher-img">
              <img
                :src="
                  'http://140.134.140.35/ds/api/teacher/get_ds_data/img/' +
                  term.emp_id +
                  '.png?202010152037'
                "
              />
            </div>

            <div class="card-body">
              <div class="card-title">
                <h5>{{ term.name }}</h5>
                <h6>| {{ term.tit_name }}</h6>
                <a
                  type="button"
                  class="btn btn-outline-secondary btn-sm ml-2"
                  :href="'#/teacher/container?' + term.emp_id"
                  >詳細介紹</a
                >
              </div>

              <div class="card-text">
                <p v-if="term.emp_email">
                  <span class="detail">信箱：</span
                  >{{ eventEmail(term.emp_email, true) }}<i class="fa fa-at"></i
                  >{{ eventEmail(term.emp_email, false) }}
                </p>
                <p>
                  <span class="detail">專長：</span
                  ><span v-for="skill in term.skill" :key="skill.ski_no"
                    >{{ eventText(skill.ski_desc_c) }} |
                  </span>
                </p>
                <p><span class="detail">分機：</span>{{ term.emp_ext }}</p>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import Title from "../components/Title";
export default {
  data() {
    return {
      teacher: [],
      nowTime: 1,
    };
  },
  components: {
    Title,
  },
  created: function () {
    this.nowTime = Date.now();
  },
  mounted() {
    this.getTeacherData();
  },
  methods: {
    eventText(text) {
      return text.replace("</br>", "");
    },
    eventEmail(email, type) {
      if (!email) return;
      if (type) {
        return email.split("@")[0];
      }
      return email.split("@")[1];
    },
    /**https://cors-anywhere.herokuapp.com/http://www.math.fcu.edu.tw/ds/ */
    getTeacherData: async function () {
      await this.$axios
        .get(
          "http://www.math.fcu.edu.tw/ds/api/teacher/get_ds_data/Teacher.json"
        )
        .then((res) => {
          this.sortTeacher(res.data);
        });
    },
    sortTeacher: function (teacher) {
      var newTeacher = [];
      for (var a = 0; a < teacher.length; a++) {
        if (teacher[a].tit_name === "特聘教授") {
          newTeacher.push(teacher[a]);
        }
      }
      for (var i = 0; i < teacher.length; i++) {
        if (teacher[i].tit_name === "教授") {
          newTeacher.push(teacher[i]);
        }
      }
      for (var j = 0; j < teacher.length; j++) {
        if (teacher[j].tit_name === "副教授") {
          newTeacher.push(teacher[j]);
        }
      }
      for (var k = 0; k < teacher.length; k++) {
        if (teacher[k].tit_name === "助理教授") {
          newTeacher.push(teacher[k]);
        }
      }
      this.teacher = newTeacher;
    },
  },
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
h5,
.detail {
  font-weight: 800;
  margin-right: 5px;
}
h6 {
  color: #495057;
}
img {
  padding-top: 20px;
  width: 250px;
}
.teacher-img {
  overflow: hidden;
}

.card-text {
  text-align: left;
}
p {
  margin: 0px;
}

.card-title {
  display: flex;
  justify-content: center;
  align-items: flex-end;
}
</style>
