<template>
  <div>
    <Title title='highlight' />
    <div class="container">
      <TablePage :datas=highlight label='highlight'/>
    </div>
  </div>
</template>

<script>
import Title from '../components/Title'
import TablePage from '../components/Table'
export default {
  components: {
    Title,
    TablePage
  },
  data() {
    return {
      highlight: [],
    }
  },
  mounted() {
    this.getHighlighttData();
  },
  methods: {
    /**https://cors-anywhere.herokuapp.com/ */
    getHighlighttData: function () {
      this.$axios.get('api/news/highlight.json').then(res=>{       
        this.highlight = res.data
      })
    },
  }
}
</script>

